@import '../../../style/common.less';

@action-list-prefix-cls: ~'@{mi-prefix}-action-list';

.@{action-list-prefix-cls} {
  &-action-btn {
    cursor: pointer;
    &.commonText {
      color: @primary-color;
    }
    &.disabledText {
      color: #ccc;
    }
    &:not(:last-child) {
      &:after {
        padding: 0 4px;
        color: #e5e5e5;
        content: '';
      }
    }
  }
  &-more-svg {
    path {
      fill: @primary-color;
    }
  }
  &-popover {
    .ant-popover-inner-content {
      padding: 4px 0;
      min-width: 96px;
    }
  }
  &-popover-btn {
    display: inline-block;
    padding: 7px 12px;
    line-height: 20px;
    width: 100%;
    &:hover {
      background-color: #f5f5f5;
    }
  }
}

@primary-color: #1890ff;